import { HttpClientModule } from '@angular/common/http';
import { APP_INITIALIZER, ModuleWithProviders, NgModule, Optional, SkipSelf } from '@angular/core';
import { AuthConfig, OAuthModule, OAuthModuleConfig, OAuthStorage } from 'angular-oauth2-oidc';
import { AuthGuardWithForcedLogin } from './auth-guard-with-forced-login.service';
import { AuthGuard } from './auth-guard.service';
import { AuthService } from './auth.service';

const authAppInitializerFactory = (authService: AuthService):Promise<void> =>   {
  return authService.runInitialLoginSequence();
}

const authConfig: AuthConfig = {
  issuer: 'https://demo.duendesoftware.com', // M√°y ch·ªß c·ªßa t√¥i
  clientId: 'interactive.public', // The "Auth Code + PKCE" client
  responseType: 'code',
  redirectUri: window.location.origin + '/angular-oauth2-oidc/', // ƒêƒÉng nh·∫≠p th√†nh c√¥ng r·ªìi quay l·∫°i v·ªõi url
  silentRefreshRedirectUri: window.location.origin + '/silent-refresh.html',
  scope: 'openid profile email api', // Y√™u c·∫ßu offline_access h·ªó tr·ª£ l√†m m·ªõi m√£ th√¥ng b√°o l√†m m·ªõi
  useSilentRefresh: true, // C·∫ßn thi·∫øt ƒë·ªÉ Lu·ªìng m√£ ƒë·ªÅ xu·∫•t s·ª≠ d·ª•ng l√†m m·ªõi d·ª±a tr√™n khung n·ªôi tuy·∫øn
  silentRefreshTimeout: 5000, // ƒê·ªÉ ki·ªÉm tra nhanh h∆°n
  timeoutFactor: 0.25, // ƒê·ªÉ ki·ªÉm tra nhanh h∆°n
  sessionChecksEnabled: true,
  showDebugInformation: true, // C≈©ng y√™u c·∫ßu b·∫≠t c·∫•p ƒë·ªô "Verbose" trong devtools
  clearHashAfterLogin: false, // https://github.com/manfredsteyer/angular-oauth2-oidc/issues/457#issuecomment-431807040,
  nonceStateSeparator: 'semicolon' // D·∫•u ch·∫•m ph·∫©y th·ª±c b·ªã x√°o tr·ªôn b·ªüi m√£ h√≥a URI c·ªßa M√°y ch·ªß ID Duende
};

const authModuleConfig: OAuthModuleConfig = {
  resourceServer: {
    allowedUrls: ['https://demo.duendesoftware.com/api'], // API M√°y ch·ªß c·ªßa t√¥i
    sendAccessToken: true,
  }
};

// Ch√∫ng t√¥i c·∫ßn m·ªôt factory v√¨ localStorage kh√¥ng kh·∫£ d·ª•ng t·∫°i th·ªùi ƒëi·ªÉm x√¢y d·ª±ng AOT
const storageFactory = (): OAuthStorage => {
  return localStorage;
}


@NgModule({
  imports: [
    HttpClientModule,
    OAuthModule.forRoot(),
  ],
  providers: [
    AuthService,
    AuthGuard,
    AuthGuardWithForcedLogin,
  ],
})
export class CoreModule {
  static forRoot(): ModuleWithProviders<CoreModule> {
    return {
      ngModule: CoreModule,
      providers: [
        { provide: APP_INITIALIZER, useFactory: authAppInitializerFactory, deps: [AuthService], multi: true },
        { provide: AuthConfig, useValue: authConfig },
        { provide: OAuthModuleConfig, useValue: authModuleConfig },
        { provide: OAuthStorage, useFactory: storageFactory },
      ]
    };
  }

  constructor(@Optional() @SkipSelf() parentModule: CoreModule) {
    if (parentModule) {
      throw new Error('CoreModule is already loaded. Import it in the AppModule only');
    }
  }
}
